import React, { createContext, useContext, useReducer } from "react";

const INITIAL_STATE = {
  plan: "",
  yearly: false,
  addOns: [],
  activeNav: "",
  changePlan: (plan) => {},
  toggleYearly: () => {},
  updateAddOns: (addOn) => {},
  setActiveNav: (nav) => {},
};
const PlansContext = createContext(INITIAL_STATE);

const DEFAULT_STATE = {
  plan: "",
  yearly: false,
  addOns: [],
  activeNav: "INFO",
};

export const usePlansContext = () => {
  return useContext(PlansContext);
};

const plansReducer = (state, action) => {
  switch (action.type) {
    case "PLAN_CHANGE": {
      return { ...state, plan: action.plan };
    }
    case "TOGGLE_YEARLY": {
      return { ...state, yearly: !state.yearly };
    }
    case "UPDATE_ADDONS": {
      let updatedAddons = [];
      if (state.addOns.includes(action.addOn)) {
        updatedAddons = state.addOns.filter((addon) => addon != action.addOn);
      } else updatedAddons = [...state.addOns, action.addOn];
      return { ...state, addOns: updatedAddons };
    }
    case "CHANGE_NAV": {
      return { ...state, activeNav: action.payload };
    }
    default:
      return state;
  }
};

const PlansContextProvider = (props) => {
  const [planState, dispatch] = useReducer(plansReducer, DEFAULT_STATE);

  const changePlan = (plan) => {
    console.log(plan);
    dispatch({ type: "PLAN_CHANGE", plan: plan });
  };
  const toggleYearly = () => {
    console.log("yearly toggled", planState.yearly);
    dispatch({ type: "TOGGLE_YEARLY" });
  };

  const updateAddOns = (addOn) => {
    dispatch({ type: "UPDATE_ADDONS", addOn: addOn });
  };
  const setActiveNav = (nav) => {
    console.log("nav changed", nav);
    dispatch({ type: "CHANGE_NAV", payload: nav });
  };

  const planStateCtx = {
    plan: planState.plan,
    changePlan,
    yearly: planState.yearly,
    toggleYearly,
    addOns: planState.addOns,
    updateAddOns,
    activeNav: planState.activeNav,
    setActiveNav : setActiveNav,
  };

  return (
    <PlansContext.Provider value={{planStateCtx}}>
      {props.children}
    </PlansContext.Provider>
  );
};

export default PlansContextProvider;
