import React from "react";
import Card from "../UI/Card";
import { usePlansContext } from "../PlansContext";

const Navbar = () => {
  const {planStateCtx} = usePlansContext();
  const {activeNav, setActiveNav } = planStateCtx;

  return (
    <Card className="navbar">
      <ul className="links">
        <li
          id="INFO"
          className={`nav_item_card ${activeNav === "INFO" && "active"}`}
        >
          <span className="nav_item_circle">1</span>
          <span className="nav_item">
            <small>STEP 1</small>
            <p>YOUR INFO</p>
          </span>
        </li>
        <li
          id="PLANS"
          className={`nav_item_card ${activeNav === "PLANS" ? "active" : ""}`}
        >
          <span className="nav_item_circle">2</span>
          <span className="nav_item">
            <small>STEP 2</small>
            <p>SELECT PLAN</p>
          </span>
        </li>
        <li
          id="ADDONS"
          className={`nav_item_card ${activeNav === "ADDONS" ? "active" : ""}`}
        >
          <span className="nav_item_circle">3</span>
          <span className="nav_item">
            <small>STEP 3</small>
            <p>ADD-ONS</p>
          </span>
        </li>
        <li
          id="SUMMARY"
          className={`nav_item_card ${activeNav === "SUMMARY" ? "active" : ""}`}
        >
          <span className="nav_item_circle">4</span>
          <span className="nav_item">
            <small>STEP 4</small>
            <p>SUMMARY</p>
          </span>
        </li>
      </ul>
    </Card>
  );
};

export default Navbar;
