import React, { useEffect } from 'react'
import Title from "./Title";
import Button from "../UI/Button";
import { useNavigate } from "react-router-dom";
import Card from '../UI/Card';
import { usePlansContext } from "../PlansContext";


const AddOns = () => {
  const {planStateCtx} = usePlansContext();
  const { setActiveNav, addOns, updateAddOns } = planStateCtx;
  const title = {
    heading: "Pick add-ons",
    desc: "Add-ons help enhance your gaming experience.",
  };
  const navigate = useNavigate()
  const goNext =()=>{
    navigate('/summary');
  }
  const goBack =()=>{
    navigate('/plans');
  }
  useEffect(()=>{
    setActiveNav("ADDONS")
  },[])

  const addOnState = {
    online : false,
    larger : false,
    customizable: false
  }

  addOns.forEach(addOn =>{
    addOnState[addOn] = true;
  })

  return (
    <>
    <Title title={title} />
    <div className="content">
      <Card className={`addon ${addOnState.online && "active"}`}>
        <div>
          <input
            type="checkbox"
            id="online"
            checked={addOnState.online}
            onChange={() => updateAddOns("online")}
          />
          <label htmlFor="online">
            <p>Online Service</p>
            <span>Access to multiplayer games</span>
          </label>
        </div>
        <span className="price">+$1/mo</span>
      </Card>
      <Card className={`addon ${addOnState.larger && "active"}`}>
        <div>
          <input
            type="checkbox"
            id="larger"
            checked={addOnState.larger}
            onChange={() => updateAddOns("larger")}
          />
          <label htmlFor="larger">
            <p>Larger storage</p>
            <span>Extra 1TB of cloud save</span>
          </label>
        </div>
        <span className="price">+$2/mo</span>
      </Card>
      <Card className={`addon ${addOnState.customizable && "active"}`}>
        <div>
          <input
            type="checkbox"
            id="customizable"
            checked={addOnState.customizable}
            onChange={() => updateAddOns("customizable")}
          />
          <label htmlFor="customizable">
            <p>Customizable Profile</p>
            <span>Custom theme on your profile</span>
          </label>
        </div>
        <span className="price">+$2/mo</span>
      </Card>
    </div>
    <div className="actions">
      <Button content="Next Step" className="primary" clickHandler={goNext} />
      <Button content="Go Back" className="back-btn" clickHandler={goBack} />
    </div>
  </>
  )
}

export default AddOns