import React, { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import Button from "../UI/Button";
import Input from "../UI/Input";
import Title from "./Title";
import { usePlansContext } from "../PlansContext";

const YourInfo = () => {
  const {planStateCtx} = usePlansContext();
  const { setActiveNav } = planStateCtx;
  const title = {
    heading: "Personal Info",
    desc: "Please provide your name, email address, and phone number.",
  };
  const navigate = useNavigate()
  const goNext =()=>{
    navigate('/plans');
  }
  useEffect(()=>{
    setActiveNav("INFO")
  },[])
  return (
    <>
      <Title title={title} />
      <div className="content">
        <Input id="name" type="text" placeholder="e.g. Stephen Kings" label="Name" />
        <Input
          id="email"
          placeholder="e.g. Stephen@Kings.com"
          label="Email Address"
          type = 'email'
        />
        <Input
          id="phone"
          placeholder="e.g. +1 123 456 7890"
          label="Phone Number"
          type="number"
        />
      </div>
      <div className="actions">
        <Button content="Next Step" className="primary" clickHandler={goNext}/>
        {/* <Button content="Go Back" className='back-btn' /> */}
      </div>
    </>
  );
};

export default YourInfo;
