import React, { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import Button from "../UI/Button";
import Title from "./Title";
import Card from "../UI/Card";
import arcade from "../assets/images/icon-arcade.svg";
import advanced from "../assets/images/icon-advanced.svg";
import pro from "../assets/images/icon-pro.svg";
import { BsToggleOff, BsToggleOn } from "react-icons/bs";
import { usePlansContext } from "../PlansContext";

const Plans = () => {
  const {planStateCtx} = usePlansContext();
  const {setActiveNav, plan, yearly, changePlan, toggleYearly} = planStateCtx;
  const navigate = useNavigate();
  useEffect(()=>{
    setActiveNav("PLANS")
  },[])

  const title = {
    heading: "Select your plan",
    desc: "You have the option of monthly or yearly billing.",
  };
  const goNext =()=>{
    if(!plan)return;
    navigate('/add-ons');
  }
  const goBack =()=>{
    navigate('/');
  }

  const arcadePrice = !yearly ? (
    <>
      <span>$9/mo</span>
    </>
  ) : (
    <>
      <span>$90/yr</span>
      <small>2 months free</small>
    </>
  );
  const advancedPrice = !yearly ? (
    <>
      <span>$12/mo</span>
    </>
  ) : (
    <>
      <span>$120/yr</span>
      <small>2 months free</small>
    </>
  );
  const proPrice = !yearly ? (
    <>
      <span>$15/mo</span>
    </>
  ) : (
    <>
      <span>$150/yr</span>
      <small>2 months free</small>
    </>
  );

  return (
    <>
      <Title title={title} />
      <div className="content">
        <div className="plans">
          <Card className={`plan ${plan == "Arcade" && "active"}`} onClick={()=>planStateCtx.changePlan("Arcade")}>
            <img src={arcade} alt="Arcade" />
            <div className="price">
              <h4>Arcade</h4>
              {arcadePrice}
            </div>
          </Card>
          <Card className={`plan ${plan == "Advanced" && "active"}`} onClick={()=>changePlan("Advanced")}>
            <img src={advanced} alt="Advanced" />
            <div className="price">
              <h4>Advanced</h4>
              {advancedPrice}
            </div>
          </Card>
          <Card className={`plan ${plan == "Pro" && "active"}`} onClick={()=>changePlan("Pro")}>
            <img src={pro} alt="Pro" />
            <div className="price">
              <h4>Pro</h4>
              {proPrice}
            </div>
          </Card>
        </div>
        <Card className="price_action">
          Monthly
          {yearly ? (
            <BsToggleOn onClick={() => toggleYearly()} />
          ) : (
            <BsToggleOff onClick={() => toggleYearly()} />
          )}
          Yearly
        </Card>
      </div>
      <div className="actions">
        <Button content="Next Step" className={`primary ${plan ? "" : "disabled" }`} clickHandler={goNext} />
        <Button content="Go Back" className="back-btn" clickHandler={goBack}/>
      </div>
    </>
  );
};

export default Plans;
