import React, { useEffect } from "react";
import Title from "./Title";
import Button from "../UI/Button";
import { useNavigate, Link } from "react-router-dom";
import Card from "../UI/Card";
import { usePlansContext } from "../PlansContext";

const Summary = () => {
  const { planStateCtx } = usePlansContext();
  const { setActiveNav, addOns, plan, yearly } = planStateCtx;
  const title = {
    heading: "Finishing up",
    desc: "Double-check everything looks OK before confirming.",
  };

  const planPrice = {
    Arcade: 9,
    Advanced: 12,
    Pro: 15,
  };
  const price = `$${yearly ? planPrice[plan] * 10 : planPrice[plan]}${
    yearly ? "/yr" : "/mo"
  }`;

  const addOnInfo = {
    online: { name: "Online Service", price: 1 },
    larger: { name: "Larger Storage", price: 2 },
    customizable: { name: "Customizable Profile", price: 2 },
  };
  let addonsTotal = 0;
  addOns.forEach(addon =>{
    addonsTotal += yearly ? (addOnInfo[addon].price * 10) : addOnInfo[addon].price;
  })

  const Total = (yearly ? (planPrice[plan] * 10) : planPrice[plan]) + addonsTotal;

  const navigate = useNavigate();
  const goNext = () => {
    navigate("/finished");
  };
  const goBack = () => {
    navigate("/add-ons");
  };
  useEffect(() => {
    setActiveNav("SUMMARY");
  }, []);
  return (
    <>
      <Title title={title} />
      <div className="content">
        <Card className="summaryCard">
          <div className="item_amount">
            <span className="item">
              <h3>{`${plan} ${yearly ? "(Yearly)" : "(Monthly)"}`}</h3>
              <Link to="/plans">Change</Link>
            </span>
            <span className="amount">{price}</span>
          </div>
          {addOns.map((addon) => {
           return  <div className="item_amount">
              <span className="item">{addOnInfo[addon].name}</span>
              <span className="amount">
                {`$${yearly ? (addOnInfo[addon].price * 10) : addOnInfo[addon].price }${
                  yearly ? "/yr" : "/mo"
                }`}
              </span>
            </div>;
          })}
        </Card>
        <Card className="total">

        <span>Total ( per {`${yearly ? "year" : "month"} `} )</span>
        <h3 > +${`${Total}${yearly ? "/yr" : "/mo"}`}</h3>
        </Card>
      </div>
      <div className="actions">
        <Button content="Confirm" className="confirm" clickHandler={goNext} />
        <Button content="Go Back" className="back-btn" clickHandler={goBack} />
      </div>
    </>
  );
};

export default Summary;
